import React, { useState } from 'react';
import { slideshowData } from '../slideshow-R';
import "bootstrap/dist/css/bootstrap.min.css";
import "../styles.css";
import Slide from "./Slide"; 
import classNames from 'classnames';


export function CarouselCards(props) {
    const [viewAll, setViewAll] = useState(false);
    const [curSlide, setCurSlide] = useState(0); 
    const [showNav, setShowNav] = useState(false); 
    function setSlide(slideNum){ 
        setCurSlide(slideNum); 
        setViewAll(false); 
    }
    function toggleNav() {
        setShowNav(!showNav);
    }
    return (
        <>
        <p><b>R</b>eview what was supposed to happen:</p>
        <div id="slideshowCard" className="card">
            <div id="hamburger" className="hamburger, navbar-brand">
            <span id="hamburger" className="hamburger" onClick={() => toggleNav()}>&#9776;</span>
            </div>
            <div className="float-container">
                <div id="sideText" className={classNames("float-child-left", !showNav && "hidden")}>
                    {
                        slideshowData.map((data, index) => (
                        <p onClick={() => setSlide(index)}>{data.title}</p>
                    ))}
                    <a onClick={() => setViewAll(!viewAll)}>ALL PHASES</a>
                </div>
                <div className="card-body, slides, float-child">
                    {slideshowData.map((data, index) => (
                        <Slide key={index} title={data.title} body={data.body} show={index===curSlide||viewAll}/>
                    ))}
                </div>
            </div>
            <div className="arrows">
            <a id="prev" disabled={curSlide===0} className="prev" onClick={() => setCurSlide(curSlide-1)} >{"<"}</a> 
            <a id="next" disabled={curSlide===slideshowData.length-1} className="next" onClick={() => setCurSlide(curSlide+1)} >{">"}</a> 
            </div>

        </div>
        </>
    );
}

export default CarouselCards;
