import React from "react";
import { stepData } from "../steps";
import Card from "./Card";
function loop(n) { 
    console.log("In loop: " + n);
    for (var i in stepData) {
        console.log(stepData[i].step + ", props: " + n);
        if(stepData[i].step === n) {
            console.log(stepData[i].title);
            var retArray = [i, stepData[i].title, stepData[i].title]
            //will return json object number, title, and description
            return retArray;
        }
    }  
}

export function CardText(props) {
    //const data = steps.find(rec => rec.step === name);
    return (
        <>
        <p><b>{
        stepData[loop(props.name)[0]].title.charAt(0)}</b>
        {stepData[loop(props.name)[0]].title.substring(1, stepData[loop(props.name)[0]].length)}</p>
        {/* <Card title={data.title} desc={data.description}>
            {
                name==="E" && <ol> {data.substeps.map((ss) => (<li>{ss}</li>))} </ol>
            }
        </Card> */}
        <div className="card">
            <div className="card-body">
                <h5 className="card-title"><b>{stepData[loop(props.name)[0]].subtitle}</b></h5>
                <p className="card-text">{stepData[loop(props.name)[0]].description}</p>
                <ol>
                { props.name==="E" && 
                stepData[loop(props.name)[0]].substeps.map((data) => (
                <li>{data}</li>
                ))}
                </ol>
            </div>
        </div>
        </>
    );
}

export default CardText;